<template>
  <div class="container">
    <div class="card">
      <div class="login-top"></div>
      <!--  -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="form">
        <!--  -->
        <el-form-item prop="user"><el-input placeholder="" v-model="loginForm.user" prefix-icon="el-icon-user-solid"></el-input></el-form-item>

        <!--  -->
        <el-form-item prop="password"><el-input placeholder="" v-model="loginForm.password" prefix-icon="el-icon-lock" type="password"></el-input></el-form-item>

        <!--  -->
        <el-form-item prop="code" v-show="captchaShow">
          <div class="captcha-code">
            <el-input placeholder="" v-model="loginForm.code" prefix-icon="el-icon-key" ref="codeInput" class="code"></el-input>
            <img src="/" ref="code" alt="" width="30%" @click="getCaptcha" />
          </div>
        </el-form-item>
        <el-form-item class="btn"><el-button type="primary" @click="login"></el-button></el-form-item>
        <el-form-item class="btn">
          <a href="javascript:;" @click="oauthLogin('weixin')" title="" v-show="switchForm.qqlogin_switch">
            <img width="30" src="../assets/image/login/weixin.png" alt="" />
          </a>
          <a href="javascript:;" @click="oauthLogin('qq')" title="QQ" v-show="switchForm.weixinlogin_switch"><img width="30" src="../assets/image/login/qq.png" alt="QQ" /></a>
          <a href="javascript:;" @click="oauthLogin('weibo')" title="" v-show="switchForm.sinalogin_switch">
            <img width="30" src="../assets/image/login/weibo.png" alt="" />
          </a>
          <a href="javascript:;" @click="oauthLogin('gitee')" title="Gitee" v-show="switchForm.giteelogin_switch">
            <img width="30" src="../assets/image/login/gitee.png" alt="Gitee" />
          </a>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      loginForm: {
        user: '',
        password: '',
        code: ''
      },
      // 
      bindLogin: {
        qq: window.serverConfig.BASE_API + 'oauth/login/type/' + 'qq',
        weixin: window.serverConfig.BASE_API + 'oauth/login/type/' + 'weixin',
        weibo: window.serverConfig.BASE_API + 'oauth/login/type/' + 'sina',
        gitee: window.serverConfig.BASE_API + 'oauth/login/type/' + 'gitee'
      },
      //
      loginFormRules: {
        user: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 5,
            max: 15,
            message: ' 5  15 ',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 15,
            message: ' 6  15 ',
            trigger: 'blur'
          }
        ]
      },
      // 
      captchaShow: false,
      // 
      switchForm: {}
    };
  },
  created() {
    // 
    this.isCheckCaptcha();
    // 
    this.getSwitch();
    // 
    var self = this;
    document.onkeydown = function(e) {
      var key = window.event.keyCode;
      if (key === 13) {
        self.login();
      }
    };
  },
  methods: {
    /**
     * 
     */
    login() {
      // 
      const md5 = window.sessionStorage.getItem('md5');
      if (md5) {
        if (this.$md5(this.loginForm.code) !== md5) {
          // 
          this.$refs.codeInput.focus();
          return this.$message.error('');
        }
      }
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return;
        const { data: res } = await this.$http.post('login', this.loginForm);
        if (res.code === 401) {
          return this.$message.error(res.msg);
        } else if (res.code === 403) {
          window.sessionStorage.setItem('md5', res.data.md5);
          // 
          this.$refs.code.setAttribute('src', res.data.base64);
          // 
          this.loginForm.code = '';
          // 
          this.captchaShow = true;
          return this.$message.error(res.msg);
        } else {
          // sessionStorage
          sessionStorage.removeItem('md5');
          this.$message.success('');
          //token
          window.sessionStorage.setItem('token', res.data.Authorization);
          this.$router.push('/home');
        }
      });
    },

    /**
     * 
     */
    async getCaptcha() {
      const { data: res } = await this.$http.get('captcha');
      // 
      this.$refs.code.setAttribute('src', res.data.base64);
      // md5
      window.sessionStorage.setItem('md5', res.data.md5);
      // 
      this.loginForm.code = '';
      // 
      this.$refs.codeInput.focus();
    },

    /**
     * ,
     */
    isCheckCaptcha() {
      const chaptcha = window.sessionStorage.getItem('md5');
      if (chaptcha) {
        this.getCaptcha();
        this.captchaShow = true;
      }
    },

    /**
     * 
     */
    oauthLogin(type) {
      switch (type) {
        case 'weixin':
          window.open(this.bindLogin.weixin);
          break;
        case 'qq':
          window.open(this.bindLogin.qq);
          break;
        case 'weibo':
          window.open(this.bindLogin.weibo);
          break;
        case 'gitee':
          window.open(this.bindLogin.gitee);
          break;
        case 'github':
          window.open(this.bindLogin.github);
          break;
        default:
          this.$message.error('');
      }
      // token
      window.addEventListener(
        'message',
        function(e) {
          if (e.data !== '' && e.data.constructor !== Object) {
            const token = window.sessionStorage.getItem('token', e.data);
            if (token === '' || token === undefined || token === null) {
              window.sessionStorage.setItem('token', e.data);
            }
            window.location.reload();
          }
        },
        false
      );
    },

    /**
     * 
     */
    async getSwitch() {
      const { data: res } = await this.$http.get('getswitch');
      if (res.code !== 200) return this.$message.error(res.msg);
      this.switchForm = res.data;
    }
  }
};
</script>

<style lang="less" scoped>
.container {
  width: 100%;
  height: 100%;
  display: inline-block;
  background-image: url(../assets/image/background.jpeg);
  position: fixed;
  top: 0;
  left: 0;
  min-width: 1000px;
  z-index: 1;
  background-repeat: no-repeat;
  background-size: cover;
}
.card {
  width: 400px;
  height: 500px;
  position: absolute;
  border-radius: 5px;
  top: 50%;
  left: 50%;
  background-color: #fff;
  transform: translate(-50%, -50%);
  box-shadow: 0px 0px 5px #e2e3e5;
}
.form {
  position: absolute;
  top: 30%;
  width: 100%;
  padding: 0 15px;
  box-sizing: border-box;
}
.login-top {
  font-size: 36px;
  text-align: center;
  margin-top: 50px;
}
.btn {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  .el-button {
    width: 200px;
  }
  a {
    margin: 3px;
  }
}
.code {
  width: 50%;
}
.captcha-code {
  display: flex;
  img {
    margin-left: 10px;
  }
}
</style>
