<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="top-card">
      <el-row :gutter="20">
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <div class="grid-content bg-purple">
              <div class="card-body">
                <div class="icon money"><i class="iconfont icon-money"></i></div>
                <div class="data">
                  <p>
                    :
                    <span>{{ headForm.money_total }}</span>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <div class="grid-content bg-purple">
              <div class="card-body">
                <div class="icon developer"><i class="iconfont icon-kaifazheguanli"></i></div>
                <div class="data">
                  <p>
                    :
                    <span>{{ headForm.developer }}</span>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <div class="grid-content bg-purple">
              <div class="card-body">
                <div class="icon authorization"><i class="iconfont icon-shouquan"></i></div>
                <div class="data">
                  <p>
                    :
                    <span>{{ headForm.authorization }}</span>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="grid-content bg-purple">
            <div class="card-body">
              <div class="icon order"><i class="iconfont icon-yonghu"></i></div>
              <div class="data">
                <p>
                  :
                  <span>{{ headForm.user }}</span>
                </p>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="top-card">
      <el-row :gutter="20">
        <el-col :span="16">
          <el-card>
            <div slot="header" class="clearfix"><span></span></div>
            <!--  ECharts  DOM -->
            <div id="main"></div>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card class="box-card">
            <div slot="header" class="clearfix"><span></span></div>
            <div>
              <el-table :data="myStatusData" border style="width: 100%" :show-header="false">
                <el-table-column prop="key" label="key"></el-table-column>
                <el-table-column prop="value" label="value"></el-table-column>
              </el-table>
            </div>
          </el-card>
          <el-card>
            <div slot="header" class="clearfix">
              <span></span>
              <el-button @click="blog" class="blog" type="text"></el-button>
            </div>
            <div class="words">
              <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
              <br />
              <span class="inscribe"> </span>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <div class="top-card">
      <el-row :gutter="20">
        <el-col :span="6">
          <el-card class="box-card">
            <div slot="header" class="clearfix"><span></span></div>
            <div>
              <el-table :data="userExpenditureData" border style="width: 100%">
                <el-table-column type="index" label="" width="50"></el-table-column>
                <el-table-column prop="id" label="ID" width="50"></el-table-column>
                <el-table-column prop="user" label="" :formatter="stateFormat"></el-table-column>
                <el-table-column prop="expenditure" label=""></el-table-column>
              </el-table>
            </div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="box-card">
            <div slot="header" class="clearfix"><span></span></div>
            <div>
              <el-table :data="developerAppData" border style="width: 100%">
                <el-table-column type="index" label="" width="50"></el-table-column>
                <el-table-column prop="id" label="ID" width="50"></el-table-column>
                <el-table-column prop="level" label="">
                  <template slot-scope="scope">
                    <span v-if="scope.row.level === '0'"></span>
                    <span v-else-if="scope.row.level === '1'"></span>
                    <span v-else></span>
                  </template>
                </el-table-column>
                <el-table-column prop="app_count" label=""></el-table-column>
              </el-table>
            </div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="box-card">
            <div slot="header" class="clearfix"><span></span></div>
            <div>
              <el-table :data="appData" border style="width: 100%">
                <el-table-column type="index" label="" width="50"></el-table-column>
                <el-table-column prop="id" label="ID" width="50"></el-table-column>
                <el-table-column prop="name" label="" :formatter="stateFormat"></el-table-column>
                <el-table-column prop="download" label=""></el-table-column>
              </el-table>
            </div>
          </el-card>
        </el-col>
        <el-col :span="6">
          <el-card class="box-card">
            <div slot="header" class="clearfix"><span></span></div>
            <div>
              <el-table :data="newUserData" border style="width: 100%">
                <el-table-column type="index" label="" width="50"></el-table-column>
                <el-table-column prop="id" label="ID" width="50"></el-table-column>
                <el-table-column prop="user" label="" width="100" :formatter="stateFormat"></el-table-column>
                <el-table-column prop="create_time" label="">
                  <template slot-scope="scope">
                    {{ scope.row.create_time | date }}
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="blogDialogVisible" center width="20%">
      <div class="blog-body"><img src="../assets/image/blog.jpg" alt="" width="300" /></div>
    </el-dialog>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import _ from 'lodash';
export default {
  data() {
    return {
      // 
      blogDialogVisible: false,
      // 
      myStatusData: [],
      // 
      userExpenditureData: [],
      // 
      developerAppData: [],
      // 
      newUserData: [],
      // 
      appData: [],
      // 
      headForm: {},
      // 
      option: {
        tooltip: {
          trigger: 'axis'
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false
        },
        yAxis: {
          type: 'value'
        }
      }
    };
  },
  created() {
    this.welcome();
  },
  mounted() {
    this.$nextTick(function() {
      this.echarts();
    });
  },
  methods: {
    /**
     * 
     */
    async echarts() {
      // domecharts
      var myChart = echarts.init(document.getElementById('main'));
      const { data: res } = await this.$http.get('welcome');
      if (res.code !== 200) return this.$message.error(res.msg);
      // 
      const result = _.merge(res.data.option, this.option);
      myChart.setOption(result);
    },

    /**
     * 
     */
    async welcome() {
      const { data: res } = await this.$http.get('welcome');
      if (res.code !== 200) return this.$message.error(res.msg);
      console.log(res);
      // 
      res.data.status[3].value = this.$options.filters.date(res.data.status[3].value);
      // 
      this.myStatusData = res.data.status;
      // 
      this.headForm = res.data.head;
      // 
      this.userExpenditureData = res.data.buy_money;
      // 
      this.newUserData = res.data.new_user;
      // 
      this.appData = res.data.app_download;
      // 
      this.developerAppData = res.data.developer_data;
    },

    /**
     * 
     */
    blog() {
      // 
      this.blogDialogVisible = true;
    },

    /**
     * 
     * @param {Object} row
     * @param {Object} column
     * @param {Object} cellValue
     */
    stateFormat(row, column, cellValue) {
      if (!cellValue) return '';
      if (cellValue.length > 9) {
        //9
        return cellValue.slice(0, 9) + '...';
      }
      return cellValue;
    }
  }
};
</script>

<style lang="less" scoped>
.top-card {
  margin-top: 10px;
}
.grid-content {
  width: 400px;
  height: 110px;
  background-color: #fff;
  box-shadow: 0 0 6px #ddd;
}
.card-body {
  display: flex;
  padding: 15px;
}

.icon {
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  position: relative;
  border-radius: 3px;
}
.iconfont {
  font-size: 40px;
}
.data {
  width: 70%;
  margin: 0 10px 0 10px;
  font-size: 20px;
  color: #999;
  p > span {
    font-size: 30px;
    height: 40px;
    color: #575962;
    font-weight: bold;
  }
}
.order {
  background-color: #716aca;
}
.developer {
  background-color: #177dff;
}
.authorization {
  background-color: #67c23a;
}
.money {
  background-color: #409eff;
}
#main {
  width: 1080px;
  height: 400px;
}
.clearfix {
  font-weight: bold;
}
.el-card__header {
  padding: 15px 15px !important;
}
.blog {
  float: right;
  padding: 3px 0;
}
.blog-body {
  text-align: center;
}
.words {
  font-size: 14px;
  color: #606266;
}
.inscribe {
  position: absolute;
  right: 40px;
}
</style>
